import "../buttons";
import "../text-field";

import "./style.scss";

export default function loginFormInit() {
  const formEl = document.querySelector(".js-login-form__form");

  const submitFormHandler = (e) => {
    e.stopPropagation();
    e.preventDefault();

    /* eslint-disable no-console */
    console.log("login form submit");
  };

  formEl.addEventListener("submit", submitFormHandler);
}

loginFormInit();
